<!-- src/views/ResultList.vue -->
<template>
  <section class="p-6">
    <div class="page-shell">
      <!-- ÏÉÅÎã® ÌÉÄÏù¥ÌãÄ -->
      <header class="header-row mb-4">
        <div class="header-title">
          <h2 class="text-2xl md:text-3xl font-bold tracking-tight">
            ÏßÄÏõêÍ≤∞Í≥º Î™©Î°ù
          </h2>
        </div>

        <!-- üîò submitCodeÎ°ú ÌïÑÌÑ∞ÎêòÏñ¥ ÏûàÏùÑ ÎïåÎßå Ï†ÑÏ≤¥ Î≥¥Í∏∞ Î≤ÑÌäº -->
        <div v-if="filterSubmitCode" class="header-action">
          <MaterialButton
            color="dark"
            size="sm"
            variant="outlined"
            @click="clearSubmitFilter"
          >
            Ï†ÑÏ≤¥ Í≤∞Í≥º Î™©Î°ù Î≥¥Í∏∞
          </MaterialButton>
        </div>
      </header>

      <!-- üîç Í≤ÄÏÉâ / ÌïÑÌÑ∞ / Ï†ïÎ†¨ (ÏùºÎ∞ò Ïù¥Ïö©Ïûê Ï†úÏô∏) -->
      <div v-if="selectedRole !== 1" class="filter-row">
        <form class="filter-form" @submit.prevent="onSearch">
          <!-- Í≤ÄÏÉâ Ïù∏Ìíã -->
          <div class="filter-field filter-field--search">
            <input
              v-model="searchText"
              type="text"
              class="search-input"
              :placeholder="searchPlaceholder"
              @keyup.enter="onSearch"
            />
          </div>

          <!-- ÏÉÅÌÉú ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select
              v-model="statusFilter"
              class="select-input"
              @change="onFilterChange"
            >
              <option value="ALL">Ï†ÑÏ≤¥ ÏÉÅÌÉú</option>
              <option value="ING">ÏßÄÏõêÏ§ë</option>
              <option value="REVIEW">Í≤ÄÌÜ†Ï§ë</option>
              <option value="DONE">ÏßÄÏõêÏôÑÎ£å</option>
              <option value="RESUBMIT">Ïû¨ÏäπÏù∏ÏöîÏ≤≠</option>
              <option value="REJECT">Î∞òÎ†§</option>
            </select>
          </div>

          <!-- Ï†ïÎ†¨ ÏÖÄÎ†âÌä∏ -->
          <div class="filter-field filter-field--select select-wrapper">
            <select
              v-model="sortOption"
              class="select-input"
              @change="onFilterChange"
            >
              <option value="RESULT_RECENT">Í≤∞Í≥º ÏûëÏÑ±Ïùº ÏµúÏã†Ïàú</option>
              <option value="RESULT_OLD">Í≤∞Í≥º ÏûëÏÑ±Ïùº Ïò§ÎûòÎêúÏàú</option>
              <option value="PLAN_RECENT">Í≥ÑÌöç ÏûëÏÑ±Ïùº ÏµúÏã†Ïàú</option>
              <option value="PLAN_OLD">Í≥ÑÌöç ÏûëÏÑ±Ïùº Ïò§ÎûòÎêúÏàú</option>
              <option value="NAME">Ïù¥Î¶ÑÏàú</option>
            </select>
          </div>

          <!-- Í≤ÄÏÉâ Î≤ÑÌäº -->
          <div class="filter-field filter-field--button">
            <MaterialButton type="submit" color="dark" size="sm">
              Í≤ÄÏÉâ
            </MaterialButton>
          </div>
        </form>
      </div>

      <!-- ÏÉÅÌÉú ÌëúÏãú -->
      <div v-if="loading" class="text-gray-500 text-sm">Î∂àÎü¨Ïò§Îäî Ï§ë...</div>
      <div v-else-if="error" class="text-red-600 text-sm">
        {{ error }}
      </div>
      <div v-else-if="!plans.length" class="empty-state">
        Îì±Î°ùÎêú ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.
      </div>
      <!-- ‚úÖ 2) Îç∞Ïù¥ÌÑ∞Îäî ÏûàÎäîÎç∞, ÌïÑÌÑ∞/Í≤ÄÏÉâ Í≤∞Í≥ºÍ∞Ä 0Í±¥Ïù∏ Í≤ΩÏö∞ -->
      <div v-else-if="!filteredPlans.length" class="empty-state">
        Îì±Î°ùÎêú ÏßÄÏõêÍ≤∞Í≥ºÍ∞Ä ÏóÜÏäµÎãàÎã§.
      </div>

      <!-- Î™©Î°ù -->
      <div v-else class="table-wrapper">
        <div class="table-card">
          <table class="nice-table">
            <thead>
              <tr>
                <th class="th-cell text-center w-14">No</th>
                <th class="th-cell">ÏßÄÏõêÏûê Ïù¥Î¶Ñ</th>
                <th class="th-cell">Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ</th>
                <th class="th-cell">Îã¥ÎãπÏûê Ïù¥Î¶Ñ</th>
                <th v-if="selectedRole === 4" class="th-cell">Í∏∞Í¥ÄÎ™Ö</th>
                <th class="th-cell">Í≥ÑÌöç ÏûëÏÑ±Ïùº</th>
                <th class="th-cell">Í≤∞Í≥º ÏûëÏÑ±Ïùº</th>
                <th class="th-cell">Ïö∞ÏÑ†ÏàúÏúÑ</th>
                <th class="th-cell text-center">ÏÉÅÌÉú</th>
                <th class="th-cell text-center w-28"></th>
              </tr>
            </thead>

            <tbody>
              <tr
                v-for="(row, idx) in paginatedPlans"
                :key="row.resultCode || row.planCode || idx"
                class="table-row-item"
                @click.stop="goDetail(row)"
              >
                <!-- No (ÌéòÏù¥Ïßï Î∞òÏòÅ) -->
                <td class="td-cell text-center">
                  {{ (currentPage - 1) * pageSize + idx + 1 }}
                </td>

                <!-- ÏßÄÏõêÏûê Ïù¥Î¶Ñ -->
                <td class="td-cell">
                  {{ row.childName || "Î≥∏Ïù∏" }}
                </td>

                <!-- Î≥¥Ìò∏Ïûê Ïù¥Î¶Ñ -->
                <td class="td-cell">
                  {{ row.writerName || "-" }}
                </td>

                <!-- Îã¥ÎãπÏûê Ïù¥Î¶Ñ -->
                <td class="td-cell">
                  {{ row.assiName || "-" }}
                </td>

                <!-- ÏãúÏä§ÌÖú(4)Ïùº ÎïåÎßå Í∏∞Í¥ÄÎ™Ö -->
                <td v-if="selectedRole === 4" class="td-cell">
                  {{ row.orgName || "-" }}
                </td>

                <td class="td-cell">
                  {{ formatDate(row.writtenAt) }}
                </td>

                <td class="td-cell">
                  {{
                    ["CD1", "CD3"].includes(normStatus(row.status))
                      ? "-"
                      : formatDate(row.resultWrittenAt)
                  }}
                </td>

                <!-- Ïö∞ÏÑ†ÏàúÏúÑ -->
                <td class="td-cell">
                  {{ priorityLabel(row.level) }}
                </td>

                <!-- ÏÉÅÌÉú Î∞∞ÏßÄ -->
                <td class="td-cell text-center td-status">
                  <button
                    v-if="
                      normStatus(row.status) === 'CD7' && selectedRole !== 1
                    "
                    type="button"
                    class="status-pill status-pill--rejected status-pill--clickable"
                    @click.stop="openRejectReason(row)"
                  >
                    {{ statusLabel(row.status) }}
                  </button>
                  <span
                    v-else
                    class="status-pill"
                    :class="statusPillClass(row.status)"
                  >
                    {{ statusLabel(row.status) }}
                  </span>
                </td>

                <!-- ÏûëÏóÖ Î≤ÑÌäº -->
                <td class="td-cell">
                  <div class="flex items-center justify-center">
                    <!-- Îã¥ÎãπÏûê(2)Îßå Î≤ÑÌäº -->
                    <template v-if="selectedRole === 2">
                      <MaterialButton
                        v-if="['CD1', 'CD3'].includes(normStatus(row.status))"
                        color="dark"
                        size="sm"
                        @click.stop="handleWrite(row)"
                      >
                        ÏûëÏÑ±ÌïòÍ∏∞
                      </MaterialButton>

                      <MaterialButton
                        v-else-if="normStatus(row.status) === 'CD4'"
                        color="dark"
                        size="sm"
                        @click.stop="handleEdit(row)"
                      >
                        ÏàòÏ†ïÌïòÍ∏∞
                      </MaterialButton>

                      <MaterialButton
                        v-else-if="normStatus(row.status) === 'CD7'"
                        color="dark"
                        size="sm"
                        @click.stop="handleReEdit(row)"
                      >
                        Ïû¨ÏàòÏ†ïÌïòÍ∏∞
                      </MaterialButton>

                      <span v-else class="text-gray-400 text-xs"></span>
                    </template>

                    <span v-else class="text-gray-400 text-xs"></span>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <!-- ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò -->
        <div v-if="totalPages > 1" class="mt-6 text-center">
          <MaterialPagination color="dark" size="md" class="pagination">
            <MaterialPaginationItem
              prev
              :disabled="currentPage === 1"
              @click="changePage(currentPage - 1)"
            />
            <MaterialPaginationItem
              v-for="page in totalPages"
              :key="page"
              :label="String(page)"
              :active="page === currentPage"
              @click="changePage(page)"
            />
            <MaterialPaginationItem
              next
              :disabled="currentPage === totalPages"
              @click="changePage(currentPage + 1)"
            />
          </MaterialPagination>
        </div>
      </div>

      <!-- üîª Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ -->
      <div v-if="rejectReasonModalOpen" class="modal-overlay">
        <div class="modal-container">
          <h3 class="text-lg font-semibold mb-3">Î∞òÎ†§ ÏÇ¨Ïú†</h3>

          <div v-if="rejectReasonLoading" class="text-sm text-gray-500">
            Î∂àÎü¨Ïò§Îäî Ï§ë...
          </div>

          <div v-else-if="rejectReasonError" class="text-sm text-red-600">
            {{ rejectReasonError }}
          </div>

          <div v-else class="space-y-2">
            <p class="text-sm text-gray-600">
              Î∞òÎ†§ÏùºÏûê:
              <span class="font-medium">
                {{ formatDate(rejectReasonDate) }}
              </span>
            </p>

            <div
              class="text-sm whitespace-pre-line text-gray-800 max-h-60 overflow-y-auto border rounded px-3 py-2 bg-gray-50"
            >
              {{ rejectReasonText || "Îì±Î°ùÎêú Î∞òÎ†§ ÏÇ¨Ïú†Í∞Ä ÏóÜÏäµÎãàÎã§." }}
            </div>
          </div>

          <div class="modal-actions mt-4 flex justify-end gap-2">
            <MaterialButton
              color="dark"
              size="sm"
              @click="closeRejectReasonModal"
            >
              Îã´Í∏∞
            </MaterialButton>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import axios from "axios";
import { useRouter, useRoute } from "vue-router";
import MaterialButton from "@/components/MaterialButton.vue";
import MaterialPagination from "@/components/MaterialPagination.vue";
import MaterialPaginationItem from "@/components/MaterialPaginationItem.vue";

const router = useRouter();
const route = useRoute();

/** üîπ submitCode ÏøºÎ¶¨Î°ú Î∞õÏïÑÏôÄÏÑú ÌïÑÌÑ∞Ïóê ÏÇ¨Ïö© */
const filterSubmitCode = ref(route.query.submitCode || null);

// Î°úÍ∑∏Ïù∏/Ïó≠Ìï† Ï†ïÎ≥¥
const currentUserId = ref(null);
const rawAuthCode = ref(""); // AA1~AA4
const selectedRole = ref(1); // 1~4 Ïà´Ïûê Ïó≠Ìï†

function mapAuthToRole(code) {
  switch (code) {
    case "AA1":
      return 1; // ÏùºÎ∞ò
    case "AA2":
      return 2; // Îã¥ÎãπÏûê
    case "AA3":
      return 3; // Í¥ÄÎ¶¨Ïûê
    case "AA4":
      return 4; // ÏãúÏä§ÌÖú
    default:
      return 1;
  }
}

// Î™©Î°ù Îç∞Ïù¥ÌÑ∞ Î∞è ÏÉÅÌÉú
const plans = ref([]);
const loading = ref(false);
const error = ref("");

// ÎÇ†Ïßú Ìè¨Îß∑ÌÑ∞
const formatDate = (v) => {
  if (!v) return "-";
  return String(v).slice(0, 10);
};

// ÏÉÅÌÉú ÏΩîÎìú Ï†ïÍ∑úÌôî
function normStatus(raw) {
  return (raw ?? "").toString().trim().toUpperCase();
}

// Í≤∞Í≥º ÏÉÅÌÉú ÏΩîÎìú ÎùºÎ≤®
function statusLabel(code) {
  switch (normStatus(code)) {
    case "CD1":
    case "CD3":
      return "ÏßÄÏõêÏ§ë";
    case "CD4":
      return "Í≤ÄÌÜ†Ï§ë";
    case "CD5":
      return "ÏßÄÏõêÏôÑÎ£å";
    case "CD6":
      return "Ïû¨ÏäπÏù∏ÏöîÏ≤≠";
    case "CD7":
      return "Î∞òÎ†§";
    default:
      return code || "-";
  }
}

// ÏÉÅÌÉúÎ≥Ñ Î∞∞ÏßÄ Ïä§ÌÉÄÏùº
function statusPillClass(code) {
  switch (normStatus(code)) {
    case "CD1":
    case "CD3":
      return "p-gray";
    case "CD4":
      return "p-yellow";
    case "CD5":
      return "p-green";
    case "CD6":
      return "p-orange";
    case "CD7":
      return "p-red";
    default:
      return "p-gray";
  }
}

// üîç Í≤ÄÏÉâ / ÏÉÅÌÉú / Ï†ïÎ†¨ ÏÉÅÌÉú
const searchText = ref("");
const appliedSearchText = ref("");
const statusFilter = ref("ALL");
const sortOption = ref("RESULT_RECENT");

const searchPlaceholder = computed(() => {
  if (selectedRole.value === 4) {
    return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê, Í∏∞Í¥ÄÎ™Ö Í≤ÄÏÉâ";
  }
  return "ÏßÄÏõêÏûê, Î≥¥Ìò∏Ïûê, Îã¥ÎãπÏûê Í≤ÄÏÉâ";
});

// üîç Í≤ÄÏÉâ/ÌïÑÌÑ∞/Ï†ïÎ†¨ Ï†ÅÏö© Î¶¨Ïä§Ìä∏
const filteredPlans = computed(() => {
  let rows = [...plans.value];

  if (selectedRole.value === 1) {
    rows = rows.filter((row) => {
      const s = normStatus(row.status);
      return s !== "CD1" && s !== "CD3";
    });
  }

  // üîπ submitCodeÎ°ú ÌïÑÌÑ∞ (Îã§Î•∏ ÌôîÎ©¥ÏóêÏÑú ÎßÅÌÅ¨ ÌÉÄÍ≥† Îì§Ïñ¥Ïò® Í≤ΩÏö∞)
  if (filterSubmitCode.value) {
    rows = rows.filter(
      (row) => String(row.submitCode) === String(filterSubmitCode.value)
    );
  }

  // 1) Í≤ÄÏÉâ
  const q = appliedSearchText.value.trim().toLowerCase();
  if (q) {
    rows = rows.filter((row) => {
      const baseTargets = [row.childName, row.writerName, row.assiName];
      const extraTargets = selectedRole.value === 4 ? [row.orgName] : [];
      const targets = [...baseTargets, ...extraTargets];

      return targets.some((v) =>
        String(v || "")
          .toLowerCase()
          .includes(q)
      );
    });
  }

  // 2) ÏÉÅÌÉú ÌïÑÌÑ∞
  if (statusFilter.value !== "ALL") {
    rows = rows.filter((row) => {
      const s = normStatus(row.status);
      switch (statusFilter.value) {
        case "ING": // ÏßÄÏõêÏ§ë: CD1, CD3
          return s === "CD1" || s === "CD3";
        case "REVIEW": // Í≤ÄÌÜ†Ï§ë
          return s === "CD4";
        case "DONE": // ÏßÄÏõêÏôÑÎ£å
          return s === "CD5";
        case "RESUBMIT": // Ïû¨ÏäπÏù∏ÏöîÏ≤≠
          return s === "CD6";
        case "REJECT": // Î∞òÎ†§
          return s === "CD7";
        default:
          return true;
      }
    });
  }

  // 3) Ï†ïÎ†¨
  if (sortOption.value === "RESULT_RECENT") {
    rows.sort((a, b) => {
      const aDate = a.resultWrittenAt ?? "";
      const bDate = b.resultWrittenAt ?? "";
      if (aDate && bDate && aDate !== bDate) {
        return bDate.localeCompare(aDate);
      }
      const aPlan = a.writtenAt ?? "";
      const bPlan = b.writtenAt ?? "";
      if (aPlan && bPlan && aPlan !== bPlan) {
        return bPlan.localeCompare(aPlan);
      }
      return Number(b.resultCode || 0) - Number(a.resultCode || 0);
    });
  } else if (sortOption.value === "RESULT_OLD") {
    rows.sort((a, b) => {
      const aDate = a.resultWrittenAt ?? "";
      const bDate = b.resultWrittenAt ?? "";
      if (aDate && bDate && aDate !== bDate) {
        return aDate.localeCompare(bDate);
      }
      const aPlan = a.writtenAt ?? "";
      const bPlan = b.writtenAt ?? "";
      if (aPlan && bPlan && aPlan !== bPlan) {
        return aPlan.localeCompare(bPlan);
      }
      return Number(a.resultCode || 0) - Number(b.resultCode || 0);
    });
  } else if (sortOption.value === "PLAN_RECENT") {
    rows.sort((a, b) => {
      const aDate = a.writtenAt ?? "";
      const bDate = b.writtenAt ?? "";
      if (aDate && bDate && aDate !== bDate) {
        return bDate.localeCompare(aDate);
      }
      return Number(b.resultCode || 0) - Number(a.resultCode || 0);
    });
  } else if (sortOption.value === "PLAN_OLD") {
    rows.sort((a, b) => {
      const aDate = a.writtenAt ?? "";
      const bDate = b.writtenAt ?? "";
      if (aDate && bDate && aDate !== bDate) {
        return aDate.localeCompare(bDate);
      }
      return Number(a.resultCode || 0) - Number(b.resultCode || 0);
    });
  } else if (sortOption.value === "NAME") {
    rows.sort((a, b) => {
      const an = a.childName || "Î≥∏Ïù∏";
      const bn = b.childName || "Î≥∏Ïù∏";
      return an.localeCompare(bn, "ko");
    });
  }

  return rows;
});

// ÌéòÏù¥Ïßï
const currentPage = ref(1);
const pageSize = 10;

const totalPages = computed(() =>
  Math.max(1, Math.ceil(filteredPlans.value.length / pageSize) || 1)
);

const paginatedPlans = computed(() => {
  const start = (currentPage.value - 1) * pageSize;
  return filteredPlans.value.slice(start, start + pageSize);
});

function changePage(page) {
  if (page < 1 || page > totalPages.value) return;
  currentPage.value = page;
}

// Í≤ÄÏÉâ Î≤ÑÌäº / ÏóîÌÑ∞
function onSearch() {
  appliedSearchText.value = searchText.value;
  currentPage.value = 1;
}

// ÏÉÅÌÉú/Ï†ïÎ†¨ Î≥ÄÍ≤Ω
function onFilterChange() {
  currentPage.value = 1;
}

// ‚úÖ submitCode ÌïÑÌÑ∞ Ìï¥Ï†ú (ÏùºÎ∞ò Ïù¥Ïö©ÏûêÏö©)
function clearSubmitFilter() {
  filterSubmitCode.value = null;
  currentPage.value = 1;

  const newQuery = { ...route.query };
  delete newQuery.submitCode;

  router.replace({
    name: route.name,
    query: newQuery,
  });
}

// Î™©Î°ù Ï°∞Ìöå
const loadList = async () => {
  loading.value = true;
  error.value = "";
  try {
    const res = await axios.get("/api/result", {
      params: {
        role: selectedRole.value,
        userId: currentUserId.value,
      },
    });

    plans.value = res.data?.result || [];
    currentPage.value = 1;
  } catch (e) {
    console.error(e);
    error.value = e.message || "ÏßÄÏõêÍ≤∞Í≥º Î™©Î°ù Ï°∞Ìöå Ï§ë Ïò§Î•ò";
    plans.value = [];
  } finally {
    loading.value = false;
  }
};

function priorityLabel(code) {
  const c = (code || "").toUpperCase();
  switch (c) {
    case "BB1":
      return "Í∏¥Í∏â";
    case "BB2":
      return "Ï§ëÏ†ê";
    case "BB3":
      return "Í≥ÑÌöç";
    default:
      return "-";
  }
}

// Ï≤´ Î°úÎî©
onMounted(() => {
  try {
    const stored = localStorage.getItem("user");

    if (stored) {
      const u = JSON.parse(stored);

      const userCode = u.user_code ?? null;
      const auth = u.role ?? "AA1";

      currentUserId.value = userCode ? Number(userCode) : null;
      rawAuthCode.value = String(auth).toUpperCase();
      selectedRole.value = mapAuthToRole(rawAuthCode.value);
    } else {
      currentUserId.value = null;
      rawAuthCode.value = "AA1";
      selectedRole.value = mapAuthToRole("AA1");
    }
  } catch (e) {
    console.error("localStorage user ÌååÏã± Ïò§Î•ò:", e);
    currentUserId.value = null;
    rawAuthCode.value = "AA1";
    selectedRole.value = mapAuthToRole("AA1");
  }

  loadList();
});

// ÏûëÏÑ±ÌïòÍ∏∞: result-write
const handleWrite = (row) => {
  router.push({
    name: "result-write",
    params: { submitcode: row.submitCode },
  });
};

// ÏàòÏ†ïÌïòÍ∏∞: result-edit
const handleEdit = (row) => {
  router.push({
    name: "result-edit",
    params: { resultCode: row.resultCode },
    query: { planCode: row.planCode, submitCode: row.submitCode },
  });
};

// Ïû¨ÏàòÏ†ïÌïòÍ∏∞: result-edit
const handleReEdit = (row) => {
  router.push({
    name: "result-edit",
    params: { resultCode: row.resultCode },
    query: { planCode: row.planCode, submitCode: row.submitCode },
  });
};

// ÏÉÅÏÑ∏: resultDetail
function goDetail(row) {
  router.push({
    name: "resultDetail",
    params: { resultCode: row.resultCode },
    query: {
      submitCode: row.submitCode,
      planCode: row.planCode,
      role: selectedRole.value,
    },
  });
}

// Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ ÏÉÅÌÉú
const rejectReasonModalOpen = ref(false);
const rejectReasonText = ref("");
const rejectReasonDate = ref("");
const rejectReasonLoading = ref(false);
const rejectReasonError = ref("");

// Î∞òÎ†§ ÏÇ¨Ïú† Î™®Îã¨ Ïó¥Í∏∞ + ÏÑúÎ≤Ñ Ï°∞Ìöå
async function openRejectReason(row) {
  rejectReasonModalOpen.value = true;
  rejectReasonText.value = "";
  rejectReasonDate.value = "";
  rejectReasonError.value = "";
  rejectReasonLoading.value = true;

  try {
    const { data } = await axios.get(
      `/api/result/${row.resultCode}/rejection-reason`
    );

    if (data?.success === false) {
      throw new Error(data.message || "Î∞òÎ†§ ÏÇ¨Ïú†Î•º Î∂àÎü¨Ïò§ÏßÄ Î™ªÌñàÏäµÎãàÎã§.");
    }

    rejectReasonText.value =
      data?.result?.rejection_reason ?? data?.rejection_reason ?? "";

    // Î∞òÎ†§ÏùºÏûê ÌïÑÎìú
    rejectReasonDate.value =
      data?.result?.approval_date ?? data?.approval_date ?? "";
  } catch (e) {
    console.error(e);
